<template>
  <div id="WebGL1" style="width: 100%; height: 100%"></div>
</template>


<script>
import { onMounted } from "@vue/runtime-core";
import VueScene from "../script/View3D";
import { ElLoading } from "element-plus";
export default {
  setup() {
    let VRScene;
    let loadingInstance;
    onMounted(() => {
      loadingInstance = ElLoading.service({ fullscreen: true });
      VRScene = new VueScene("WebGL1");
      // VRScene.axesHelper();
      // VRScene.initState();
      VRScene.renders.shadowMap.enabled = true;
      // VRScene.renders.shadowMap.type = VRScene.RendersShadow();

      const scene = VRScene.scene;

      // const spot = VRScene.addSpotlight(0xffffff, 2, true);
      // spot.distance = 20;
      // spot.position.y = 4;
      // spot.shadow.mapSize.width = 2048;
      // spot.shadow.mapSize.height = 2048;

      // scene.add(spot);

      const pointlight = VRScene.addPointLight(0xffffff, 0.5, 100);
      pointlight.position.set(0, 4.5, 2.5);
      scene.add(pointlight);
      const light2 = VRScene.addPointLight(0xffffff, 0.5, 100);
      light2.castShadow = true;
      light2.shadow.mapSize.width = 2048;
      light2.shadow.mapSize.height = 2048;
      light2.position.set(0, 4.5, -2);
      scene.add(light2);

      const light3 = VRScene.addPointLight(0xffffff, 0.5, 100);
      light3.position.set(5, 4.5, -2);
      scene.add(light3);

      // const cube = VRScene.createBox(1, { color: "red" });
      // scene.add(cube);

      let doorGroup = [];
      async function SceneModel() {
        const House = await VRScene.LoadModel("./resources/house.glb");
        House.scene.traverse((child) => {
          if (child.isMesh) {
            // console.log(child.name, "ddd");
            if (child.parent.name == "mesh_node011") {
              doorGroup.push(child);
            } else {
              child.material = VRScene.SetMaterial({
                color: child.material.color,
                map: child.material.map,
              });
            }
            if (
              child.name == "mesh_node134" ||
              child.name == "mesh_node133" ||
              child.name == "mesh_node132"
            ) {
              child.castShadow = false;
            } else {
              child.castShadow = true;
            }

            child.receiveShadow = true;
          }
        });
        scene.add(House.scene);
      }

      SceneModel().then(() => {
        doorGroup[1].material = VRScene.SetMaterial({
          color: "skyblue",
          transparent: true,
          opacity: 0.6,
        });

        loadingInstance.close();
        // setInterval(() => {
          // console.log("你好");
        // }, 3000);
      });

      VRScene.EnterVR();

      console.log("VR模型文件提供博主首页:https://webgl.blog.csdn.net/");
    });
  },
};
</script>