<template>
  <div id="WebGL" style="width: 100%; height: 100%"></div>
</template>


<script>
import { onMounted } from "vue";
// import { ref } from "vue";
import { ElLoading } from "element-plus";
import VueScene from "../script/View3D";

export default {
  setup() {
    let loadingInstance;
    onMounted(() => {
      loadingInstance = ElLoading.service({ fullscreen: true });
      let VScene = new VueScene("WebGL");
      // VScene.axesHelper();
      VScene.camera.position.set(0.35, 0.02, 0.07);
      VScene.camera.lookAt(0, -0.1, -0.2);
      VScene.control.target = VScene.Vec3Obj(0, -0.1, -0.2);

      // setInterval(()=>{
      //   console.log(VScene.camera.position,'相机位置');
      //   console.log(VScene.control.target);
      // },1000)

      let lights = VScene.AddHemisphereLight();
      VScene.scene.add(lights);

      let light1 = VScene.addSpotlight();
      light1.position.y = 1;
      let lightsTarget = VScene.Obj3D();
      VScene.scene.add(lightsTarget);
      lightsTarget.position.set(0, 0, -0.2);
      light1.target = lightsTarget;
      VScene.scene.add(light1);

      // let texts;

      let cubes = VScene.createBox(0.01, { color: "red" });
      // VScene.scene.add(cubes);
      cubes.position.set(0, 0, -0.2);

      // async function SceneObject() {
      //   texts = await VScene.TextModel("Hello，欢迎来到我的AR元宇宙", {
      //     color: "#6f7ed9",
      //     metalness: 0.4,
      //     roughness: 0.3,
      //     // wireframe: true
      //   });
      //   texts.position.set(-0.1, -0.15, -0.25);
      //   texts.scale.set(0.001, 0.001, 0.001);
      //   // VScene.scene.add(texts);

      //   // var u = navigator.userAgent;
      //   // console.log(u, "浏览器标识");
      //   // if (u.indexOf("iPhone") > -1 || u.indexOf("Mac") > -1) {
      //   //   VScene.ARforApple();
      //   //   VScene.ARMeshForApple(texts);
      //   // } else {
      //   //   VScene.ARmode("移动光标"); //ARmode('移动光标')移动光标模式
      //   //   VScene.addAREvent();
      //   // }
      // }
      let mixer;

      async function Sceneobject2() {
        let groups = VScene.SetGroup();
        groups.name = "无人机组";

        let dracoloaders = VScene.DracoLoaders("./draco/gltf/");

        let Toyk = await VScene.LoadModel(
          "./resources/LittlestTokyo.glb",
          dracoloaders
        );

        let ToykForApple = await VScene.LoadModel(
          "./resources/LittlestTokyoForApple.glb"
        );
        ToykForApple.scene.scale.set(0.1, 0.1, 0.1);

        Toyk.scene.traverse((child) => {
          if (child.isMesh) {
            child.material = VScene.SetMaterial({
              // color: 0xffffff,
              map: child.material.map,
              emissiveMap: child.material.emissiveMap,
              metalness: 0,
              roughness: 0.2,
            });
          }
        });

        Toyk.scene.rotation.y = Math.PI;
        Toyk.scene.scale.set(0.01, 0.01, 0.01);
        Toyk.scene.position.set(0, 0.1, 0);

        mixer = VScene.GetMixer(Toyk.scene);

        ToykForApple.scene.scale.set(0.01, 0.01, 0.01);
        groups.add(Toyk.scene);

        groups.scale.set(0.02, 0.02, 0.02);
        groups.position.set(0, -0.1, -0.2);
        VScene.scene.add(groups);
        // console.log(Toyk.animations[0]);
        mixer.clipAction(Toyk.animations[0]).play();

        var u = navigator.userAgent;
        // console.log(u, "浏览器标识");
        if (u.indexOf("iPhone") > -1 || u.indexOf("Mac") > -1) {
          VScene.ARforApple();
          VScene.ARMeshForApple(ToykForApple.scene);
        } else {
          VScene.ARmode(); //ARmode('移动光标')移动光标模式

          // VScene.addAREvent("select", () => {
          //   console.log("你好啊");
          //   // mixer.clipAction(Toyk.animations[1]).stop()
          // });
          // VScene.addAREvent()
        }
      }

      let clock = VScene.Clocks();
      function mixUpdate() {
        requestAnimationFrame(mixUpdate);
        const delta = clock.getDelta();
        if (mixer) {
          mixer.update(delta);
        }
      }
      mixUpdate();

      console.log(
        "AR模型文件来源threejs管网案例:https://threejs.org/examples/#webgl_animation_keyframes"
      );
      // SceneObject();
      Sceneobject2().then(() => {
        loadingInstance.close();
      });
    });
  },
};
</script>

<style scoped>
button {
  background-color: burlywood;
  border-radius: 10px;
  border: 2px;
}
</style>