
<template>
  <div class="common-layout">
    <el-container>
      <el-container>
        <el-header class="titles">{{ demoTitle }}</el-header>
        <el-container>
          <el-container>
            <el-main>
              <!-- <keep-alive>
               <router-view class="View3D" />
              </keep-alive>
              -->
              <!-- 组件keep-alive保留数据缓存 -->
              <router-view v-slot="{ Component }">
                <keep-alive>
                  <component :is="Component"></component>
                </keep-alive>
              </router-view>
              <!-- 组件keep-alive保留数据缓存 -->
            </el-main>
            <!-- <el-footer>Footer</el-footer> -->
          </el-container>
        </el-container>
      </el-container>
    </el-container>
    <Hovers @event="demoChange" class="buts" />
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import Hovers from "@/components/hovers.vue";

export default {
  name: "我的Vue页面",
  components: {
    Hovers,
  },
  setup() {
    const state = reactive({
      demoTitle: "AR房子",
      direction: "ltr",
    });

    const demoChange = (sondata) => {
      state.demoTitle = sondata;
    };

    return {
      ...toRefs(state),
      demoChange,
    };
  },
};
</script>

<style>
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: var(--el-text-color-primary);
  text-align: center;
  line-height: 60px;
}

.el-main {
  background-color: #e9eef3;
  color: var(--el-text-color-primary);
  height: 100vh;
  width: 100vw;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.grid {
  border-radius: 5px;
  min-height: 36px;
  text-align: center;
  background-color: aquamarine;
}

.buts {
  position: absolute;
  top: 20px;
  left: 10px;
}

.View3D {
  height: 100%;
  width: 100%;
}
.titles {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-weight: bold;
}
</style>

